.page{
 @media screen and (min-width: 992px) {
   height: 100vh;
   overflow: hidden;
 }
  &__swiper{
    @media screen and (min-width: 992px) {
      height: 100%;
    }
  }
  &__screen {
    @media screen and (min-width: 992px) {
      height: 100vh !important;
    }
  }
  &__nav {
    position: absolute;
    z-index: 7;
    top: 50%;
    right: -15px;
    transform: translate(0, -50%);
    background: rgba(11, 14, 20, 0.5);;
    padding: 50px 60px 50px 25px;
  }
  &__swiper-pagination {
    position: static;
    transform: unset !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .swiper-pagination-bullet {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      width: auto;
      height: auto;
      border-radius: 0;
      background: transparent;
      margin: 0 !important;
      opacity: 1;
      &:not(:first-child) {
        margin-top: 15px !important;
      }
    }
    .swiper-pagination-bullet-active {
      font-weight: 700;
    }
  }
  &__swiper-scrollbar {
    top: 50px !important;
    right: 32px !important;
    transform: rotate(360deg);
    width: 1px !important;
    height: calc(100% - 90px) !important;
    background: #22A3DA;
    .swiper-scrollbar-drag {
      left: -0.5px;
      width: 3px;
      background: #22A3DA;
    }
  }
}
.content {

}
.main {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 330px;
  &__title {
    font-size: 77px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__text {
    font-size: 27px;
    line-height: 120%;
    font-weight: 400;
    text-transform: uppercase;
    color: #FFF;
    margin-top: 30px;
  }
  &__btn {
    width: 170px;
    height: 45px;
    border: 1px solid #22A3DA;
    border-radius: 5px;
    margin-top: 30px;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    &:hover {
      background: #22A3DA;
    }
  }
  &__bottom {
    background: rgba(11, 14, 20, 0.4);
    padding: 30px 0;
    margin-top: 210px;
  }
  @media screen and (min-width: 768px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(4,calc((100% / 4) - (40px * 3 / 4)));
      gap: 40px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-pagination {
      display: none;
    }
  }
  &-slide {
    position: relative;
    &:not(:first-child) {
      padding-left: 40px;
      &:after {
        content: '';
        position: absolute;
        top: 45px;
        left: 0;
        width: 1px;
        height: 50px;
        background: #868686;
      }
    }
    &__title {
      font-size: 52px;
      line-height: 120%;
      font-weight: 300;
      color: #ffffff;
    }
    &__text {
      font-size: 25px;
      line-height: 140%;
      font-weight: 300;
      color: #FFF;
      margin-top: 5px;
    }
  }
}
.about {
  background-image: url("../img/about-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 200px;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(51, 58, 74, 0.6);
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: -500px;
    left: -400px;
    width: 1000px;
    height: 800px;
    background: radial-gradient(50% 50% at 50% 50%, #112A5A 0%, rgba(11, 14, 20, 0.00) 100%);
    border-radius: 1053.2px;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 1;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  &__left {
    width: 650px;
    margin-right: 50px;
  }
  &__right {
    width: calc(100% - 700px);
  }
  &__title {
    color: #ffffff;
  }
  &__text {
    margin-top: 25px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
  &__btn {
    width: 190px;
    height: 45px;
    border: 1px solid #22A3DA;
    border-radius: 5px;
    margin-top: 25px;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    &:hover {
      background: #22A3DA;
    }
  }
  &__slider {
    max-width: 640px;
    margin: 0 0 0 auto;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: -80px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 700px;
      height: 900px;
      border-radius: 992px;
      background: radial-gradient(50% 50% at 50% 50%, #112A5A 0%, rgba(11, 14, 20, 0.00) 100%);
      pointer-events: none;
    }
  }
  &-slide {
    img {
      width: 100%;
      border-radius: 8px;
    }
  }
  .swiper-pagination {
    bottom: -45px;
  }
  &__bottom {
    margin-top: 100px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 25px / 2));
    gap: 25px;
  }
  &__item {
    position: relative;
    padding-left: 25px;
    padding-bottom: 80px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 1px;
      height: 100%;
      opacity: 0.3;
      background: #22A3DA;
    }
    &-title {
      font-size: 23px;
      line-height: 120%;
      font-weight: 700;;
      text-transform: uppercase;
      color: #FFF;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 50px;
      padding-left: 45px;
      li {
        font-size: 18px;
        line-height: 120%;
        color: #ffffff;
        position: relative;
        padding-left: 30px;
        &:after {
          content: '';
          position: absolute;
          top: 9px;
          left: 0;
          width: 15px;
          height: 1px;
          background: #22A3DA;
        }
      }
    }
  }
}
.products {
  padding: 200px 0 130px;
  &__slider {
    position: relative;
    margin-top: 100px;
  }
  @media screen and (min-width: 768px) {
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(6,calc((100% / 6) - (40px * 5 / 6)));
      gap: 100px 40px;
      transform: unset !important;
    }
    .swiper-slide{
      width: 100% !important;
    }
    .swiper-pagination {
      display: none;
    }
  }
}
.product-slide {
  border: 1px solid transparent;
  border-radius: 8px;
  padding: 15px 35px;
  text-align: center;
  transition: all 0.5s;
  &:hover {
    border: 1px solid #BFBFBF;
    background: rgba(255, 255, 255, 0.28);
  }
  &__img {
    width: 135px;
    height: 135px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    img {
      max-width: 100%;
    }
  }
  &__title {
    font-size: 16px;
    line-height: 120%;
    color: #0B0E14;
    margin-top: 15px;
  }
}
.info {
  background: #5A81CF;
  padding: 200px 0 60px;
  overflow: hidden;
  position: relative;
  &-bg {
    span:nth-child(1) {
      position: absolute;
      z-index: 1;
      top: 50%;
      right: -300px;
      transform: translate(0, -50%);
      width: 1200px;
      height: 1200px;
      background: radial-gradient(50% 50% at 50% 50%, #3D69C1 0%, rgba(43, 81, 156, 0.00) 100%);
      pointer-events: none;
    }
    span:nth-child(2) {
      content: '';
      position: absolute;
      z-index: 1;
      left: -900px;
      bottom: -900px;
      width: 1200px;
      height: 1200px;
      background: radial-gradient(50% 50% at 50% 50%, #3D69C1 0%, rgba(43, 81, 156, 0.00) 100%);
      pointer-events: none;
    }
    span:nth-child(3) {
      content: '';
      position: absolute;
      z-index: 1;
      top: -300px;
      left: -300px;
      width: 600px;
      height: 600px;
      background: radial-gradient(50% 50% at 50% 50%, #3D69C1 0%, rgba(43, 81, 156, 0.00) 100%);
      pointer-events: none;
    }
  }
  &-img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 150px;
    pointer-events: none;
  }
  &-img2 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 890px;
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    text-transform: uppercase;
    color: #ffffff;
  }
  &__wrap {
    margin-top: 25px;
  }
  &__subtitle {
    font-size: 23px;
    line-height: 120%;
    font-style: normal;
    font-weight: 700;
    text-transform: uppercase;
    color: #FFF;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(100% / 4 - 300px / 4));
    gap: 35px 100px;
    grid-auto-rows: 1fr;
    margin-top: 40px;
  }
  &__item {
    iframe {
      width: 100%;
      height: 100%;
    }
    &-img {
      height: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      padding-bottom: 15px;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 86px;
        height: 1px;
        background: #22A3DA;
      }
    }
    &-title {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
    }
    &-btn {
      flex-shrink: 0;
      font-size: 40px;
      line-height: 100%;
      color: #22A3DA;
      cursor: pointer;
      margin-left: 10px;
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      font-weight: 300;
      color: #ffffff;
      margin-top: 15px;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:5;
      -webkit-box-orient: vertical;
    }
  }
  &__item2 {
    position: relative;
    .info__subtitle {
      position: absolute;
      top: -68px;
      left: 0;
    }
  }
  .bg-item {
    background: #2E4168;
    border: 1px solid #292A2D;
    border-radius: 8px;
    padding: 15px;
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 200px;
    margin-top: 35px;
  }
  &__btn {
    height: 45px;
    border: 1px solid #22A3DA;
    border-radius: 5px;
    padding: 5px 35px;
    &:not(:first-child) {
      margin-left: 30px;
    }
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    &:hover {
      background: #22A3DA;
    }
  }
}
.projects {
  min-height: 1000px;
  position: relative;
  padding-top: 200px;
  &__map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    .map2{
      width: 100%;
      height: 100%;
      .ymaps-2-1-79-map {
        width: 100% !important;
        height: 100% !important;
      }
    }
    .ymaps-2-1-79-balloon__layout {
      background: #2E4168;
      border: 1px solid #5B5B5B;
      border-radius: 8px;
    }
    .ymaps-2-1-79-balloon__content {
      max-width: 300px;
      background: transparent;
      padding: 30px 20px !important;
      margin-right: 0 !important;
    }
    .balloon {
      max-width: 300px;
    }
    .balloon__title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
      padding-left: 20px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 10px;
        height: 1px;
        background: #22A3DA;
      }
    }
    .balloon__img {
      margin-top: 20px;
      img {
        width: 100%;
        border-radius: 8px;
      }
    }
    .balloon__text {
      font-size: 14px;
      line-height: 120%;
      color: #ffffff;
      margin-top: 10px;
    }
    .ymaps-2-1-79-balloon__close-button {
      width: 24px;
      height: 24px;
      background-image: url("../img/close.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      padding: 7px;
      opacity: 1;
    }
  }
  &__bottom {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background: rgba(11, 14, 20, 0.8);
    padding: 20px 0;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &-slide {
    max-width: 280px;
    width: auto !important;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
    }
  }
}
.contacts {
  background-image: url("../img/contacts-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 200px 0 230px;
  overflow: hidden;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    left: -800px;
    bottom: -800px;
    width: 1200px;
    height: 1400px;
    background: radial-gradient(50% 50% at 50% 50%, #4468AF 0%, rgba(87, 117, 177, 0.00) 100%);
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__title {
    color: #ffffff;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    margin-top: 25px;
  }
  &__left {
    width: 430px;
    margin-right: 70px;
  }
  &__right {
    width: calc(100% - 500px);
  }
  &__subtitle {
    font-size: 22px;
    line-height: 120%;
    font-weight: 700;
    color: #FFF;
  }
  &__info {
    font-size: 19px;
    line-height: 120%;
    color: #ffffff;
    margin-top: 15px;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    &-title {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        width: 28px;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 20px;
        line-height: 120%;
        color: #22A3DA;
        margin-left: 10px;
      }
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      margin-top: 10px;
    }
    .location {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
    }
    .address {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
      span {
        font-weight: 700;
      }
    }
    .tel {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
    }
    .mail {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
    }
  }
  &__socials {
    display: inline-flex;
    align-items: center;
    margin-top: 45px;
  }
  &__social {
    width: 25px;
    &:not(:first-child) {
      margin-left: 12px;
    }
    img {
      width: 100%;
    }
  }
  &__btns {
    margin-top: 45px;
  }
  &__btn {
    width: 300px;
    height: 45px;
    border: 1px solid #22A3DA;
    border-radius: 5px;
    &:not(:first-child) {
      margin-top: 15px;
    }
    span {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
    }
    &:hover {
      background: #22A3DA;
    }
  }
  .map{
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
    .ymaps-2-1-79-map ymaps {
      border-radius: 8px;
    }
  }
  &__bottom {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #151C29;
    border: 1px solid #3C3C3C;
    padding: 40px 15px;
  }
  &__brevis {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 39px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 15px;
      line-height: 120%;
      color: #A7A7A7;
      margin-left: 14px;
    }
  }
}

@media screen and (max-width: 1500px) {
  .info {
    &__items {
      grid-template-columns: repeat(4, calc(25% - 150px / 4));
      gap: 30px 50px;
    }
  }
}

@media screen and (max-width: 1400px) {
  .main {
    padding-top: 180px;
    &__title {
      font-size: 66px;
    }
    &__bottom {
      padding: 40px 0;
      margin-top: 100px;
    }
    &-slide {
      &:not(:first-child) {
        &:after {
          top: 30px;
        }
      }
      &__title {
        font-size: 40px;
      }
      &__text {
        font-size: 20px;
      }
    }
  }
  .products {
    &__slider {
      margin-top: 35px;
    }
    .swiper-wrapper {
      grid-template-columns: repeat(6,calc((100% / 6) - (5px * 5 / 6)));
      gap: 20px 5px;
    }
  }
  .info {
    &-img {
      width: 100px;
    }
    &-img2 {
      width: 600px;
    }
    &__wrap {
      margin-top: 20px;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 100px / 3));
      margin-top: 20px;
    }
    &__item {
      &:nth-child(3) {
        display: none;
      }
      &:nth-child(7) {
        display: none;
      }
      &:nth-child(7) ~ .info__item {
        display: none;
      }
      &:nth-child(8) {
        display: block !important;
      }
    }
    &__item2 {
      .info__subtitle {
        top: -45px;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .about {
    &__left {
      width: 450px;
    }
    &__right {
      width: calc(100% - 500px);
    }
    &__text {
      p {
        &:nth-child(1) ~ p {
          display: none;
        }
      }
    }
    &__bottom {
      margin-top: 80px;
    }
    &__item {
      &-title {
        font-size: 21px;
      }
      &-list {
        margin-top: 40px;
      }
    }
  }
  .info {
    &-img {
      width: 70px;
    }
    &-img2 {
      width: 400px;
    }
    &__bottom {
      padding: 0 100px;
      margin-top: 20px;
    }
  }
  .projects {
    min-height: 800px;
  }
  .contacts {
    &__btns {
      display: flex;
      align-items: center;
    }
    &__btn {
      flex-shrink: 0;
      &:not(:first-child) {
        margin: 0 0 0 15px;
      }
    }
    .map {
      height: 350px;
    }
  }
}

@media screen and (max-width: 1199px) {
  .main {
    &__title {
      font-size: 52px;
    }
    &__text {
      font-size: 22px;
    }
    &-slide {
      &__title {
        font-size: 32px;
      }
      &__text {
        font-size: 16px;
      }
    }
  }
  .about {
    &__btn {
      width: 170px;
      height: 40px;
      span {
        font-size: 15px;
      }
    }
    &__item {
      padding-bottom: 50px;
      &-list {
        li {
          font-size: 15px;
        }
      }
    }
  }
  .products {
    padding-bottom: 40px;
    .swiper-wrapper {
      grid-template-columns: repeat(4,calc((100% / 4) - (20px * 3 / 4)));
      gap: 20px;
    }
  }
  .info {
    &__items {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 35px 20px;
    }
    &__bottom {
      padding: 0 50px;
    }
    &__btn {
      span {
        font-size: 15px;
      }
    }
  }
  .projects {
    min-height: 600px;
    &-slide {
      max-width: 150px;
    }
  }
  .contacts {
    &__content {
      margin-top: 15px;
    }
    &__info {
      font-size: 16px;
      margin-top: 10px;
    }
    &__items {
      gap: 15px;
      margin-top: 15px;
    }
    &__item {
      &-title {
        span {
          font-size: 16px;
        }
      }
      .location,
      .address,
      .tel,
      .mail {
        font-size: 16px;
      }
    }
    &__socials {
      margin-top: 15px;
    }
