.header {
  background: rgba(39, 39, 39, 0.75);
  padding: 30px 0;
  .container {
    max-width: 1680px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__center {
    margin: 0 15px;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__burger {
    width: 37px;
    height: 22px;
    position: relative;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #22A3DA;
      border-radius: 3px;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
  }
  &__logo {
    width: 180px;
    margin-left: 70px;
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li:not(:first-child) {
      margin-left: 18px;
    }
    .active {
      color: #22A3DA;
    }
  }
  &__link {
    display: flex;
    font-size: 18px;
    line-height: 120%;
    text-transform: uppercase;
    color: #FFF;
    a {
      display: flex;
      align-items: center;
      &:hover {
        color: #22A3DA;
      }
    }
    span {
      width: 18px;
      height: 1px;
      background: #868686;
      margin-left: 18px;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header__item-show {
        span:nth-child(1) {
          color: #22A3DA;
        }
      }
      .header__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      span:nth-child(1) {
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: #FFF;
        cursor: pointer;
      }
      span:nth-child(2) {
        width: 18px;
        height: 1px;
        background: #868686;
        margin-left: 18px;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      padding-top: 15px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-wrap {
      width: 300px;
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      background: rgba(39, 39, 39, 1);
      padding: 15px 10px;
    }
    &-link {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
      &:hover {
        color: #22A3DA;
      }
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    &:hover {
      .header__mail-text {
        color: #22A3DA;
      }
    }
    &-icon {
      flex-shrink: 0;
      width: 28px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 10px;
      transition: all 0.5s;
    }
  }
  &__tel {
    display: flex;
    align-items: center;
    margin-left: 40px;
    &-icon {
      flex-shrink: 0;
      width: 28px;
      img {
        width: 100%;
      }
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
     a {
       font-size: 18px;
       line-height: 120%;
       color: #ffffff;
       margin-left: 10px;
       transition: all 0.5s;
       &:hover {
         color: #22A3DA;
       }
     }
    }
  }
}
.header-fixed {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
}

@media screen and (max-width: 1600px) {
  .header {
    &__logo {
      width: 120px;
      margin-left: 40px;
    }
  }
}

@media screen and (max-width: 1500px) {
  .header {
    &__mail {
      display: none;
    }
    &__tel {
      margin-left: 0;
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    &__list {
      li:not(:first-child) {
        margin-left: 11px;
      }
    }
    &__link {
      span {
        width: 5px;
        margin-left: 11px;
      }
    }
    &__item {
      &-show {
        span:nth-child(2) {
          width: 5px;
          margin-left: 11px;
        }
      }
    }
  }
}

@media screen and (max-width: 1199px) {
  .header {
    &__logo {
      margin-left: 30px;
    }
    &__list {
      li {
        font-size: 15px;
      }
      li:not(:first-child) {
        margin-left: 6px;
      }
    }
    &__link {
      span {
        width: 2px;
        margin-left: 6px;
      }
    }
    &__item {
      &-show {
        span:nth-child(1) {
          font-size: 15px;
        }
        span:nth-child(2) {
          width: 2px;
          margin-left: 6px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header {
    padding: 20px 0;
    &__center {
      display: none;
    }
    &__right {
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 575px) {
  .header {
    &__logo {
      width: 100px;
      margin-left: 20px;
    }
    &__tel {
      &-icon {
        width: 20px;
      }
      &-text {
        a {
          font-size: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .header {
    &__tel {
      &-text {
        display: none;
      }
    }
  }
}