.wrap {
  overflow-x: hidden;
}
.menu {
  position: fixed;
  z-index: 10;
  top: 0;
  left: -105%;
  width: 100%;
  height: 100%;
  background: rgba(39, 39, 39, 0.95);
  padding: 100px 0;
  overflow-y: auto;
  transition: all 0.5s;
  &--active {
    left: 0;
  }
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #ffffff;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #22A3DA;
    border-radius: 3px;
  }
  &__close {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 48px;
    height: 48px;
    cursor: pointer;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__search {
    position: relative;
    input {
      width: 100%;
      height: 57px;
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      background: #3A3F42;
      border: 1px solid #3A3F42;
      border-radius: 8px;
      padding: 5px 80px 5px 18px;
      &::placeholder {
        opacity: 0.5;
      }
    }
    &-btn {
      position: absolute;
      top: 50%;
      right: 50px;
      transform: translate(0, -50%);
      width: 30px;
      background: transparent;
      padding: 0;
      img {
        width: 100%;
      }
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 90px;
  }
  .header__list {
    li {
      display: inline-block;
    }
    flex-direction: column;
    align-items: start;
    margin-top: 70px;
    li:not(:first-child) {
      margin: 35px 0 0 0;
    }
  }
  .header__link {
    a {
     flex-direction: column;
      align-items: start;
    }
    span {
      display: block;
      margin: 35px 0 0 0;
    }
  }
  .header__item {
    &--active {
      .header__item-show {
        &:after {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    &-show {
      flex-direction: column;
      align-items: start;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -15px;
        left: -10px;
        width: 250px;
        height: 50px;
        background: #3A3F42;
        opacity: 0;
        pointer-events: none;
      }
      span {
        position: relative;
        z-index: 1;
      }
      span:nth-child(2) {
        margin: 35px 0 0 0;
      }
    }
    &-hidden {
      z-index: 3;
      top: 50%;
      left: 100%;
      transform: translate(0, -50%);
      padding: 0 0 0 70px;
    }
    &-wrap {
      width: 1350px;
      display: grid;
      grid-template-columns: repeat(3, calc(100% / 3 - 120px / 3));
      gap: 60px;
      background: #3A3F42;
      border: 1px solid #22A3DA;
      border-radius: 8px;
      padding: 60px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    &:not(:last-child) {
      margin-right: 40px;
    }
    &-title {
      display: flex;
      align-items: center;
      margin-bottom: auto;
      i {
        flex-shrink: 0;
        width: 28px;
      }
      i img {
        width: 100%;
      }
      span {
        font-size: 20px;
        line-height: 120%;
        color: #22A3DA;
        margin-left: 10px;
      }
    }
    &-text {
      margin-top: 10px;
      a {
        font-size: 20px;
        line-height: 120%;
        color: #ffffff;
      }
      a:not(:first-child) {
        margin-left: 5px;
      }
      a:hover {
        color: #22A3DA;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-top: 20px;
  }
  &__social {
    max-width: 25px;
    img {
      max-width: 100%;
    }
    &:not(:first-child) {
      margin-left: 12px;
    }
  }
}
.breadcrumbs {
  padding: 30px 0 15px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    color: #22A3DA;
    margin-bottom: 15px;
    span {
      width: 15px;
      height: 1px;
      background: #22A3DA;
      margin-left: 10px;
    }
    &--active {
      color: #BDBDBD;
    }
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}
.tabs {
  &__content {
    display: none;
    &--active {
      display: block;
    }
  }
}
.display2 {
  display: inline-block;
  font-size: 28px;
  line-height: 120%;
  position: relative;
  padding-right: 150px;
  &:after {
    content: '';
    position: absolute;
    top: 15px;
    right: 0;
    width: 120px;
    height: 1px;
    background: #22A3DA;
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &-bullet {
    width: 10px;
    height: 10px;
    background: #22A3DA;
    opacity: 0.5;
    &-active {
      flex-shrink: 0;
      width: 17px;
      height: 17px;
      opacity: 1;
    }
  }
}
.navigation {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      a {
        font-size: 28px;
        line-height: 140%;
      }
      a:hover {
        color: #007aff;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .menu {
    .header__item {
      &-wrap {
        width: auto;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .menu {
    &__close {
      width: 30px;
      height: 30px;
    }
    &__search {
      input {
        height: 50px;
        padding: 5px 35px 5px 15px;
      }
      &-btn {
        right: 10px;
        width: 20px;
      }
    }
    .header__list {
      margin-top: 40px;
    }
    .header__link {
      span {
        display: none;
      }
    }
    .header__item {
      &--active {
        .header__item-hidden {
          display: block;
        }
      }
      &-show {
        span:nth-child(2) {
          display: none;
        }
        &:after {
          display: none;
        }
      }
      &-hidden {
        position: static;
        transform: unset;
        opacity: 1;
        pointer-events: all;
        padding: 15px 0 0 0;
        display: none;
      }
      &-wrap {
        grid-template-columns: 100%;
        gap: 15px;
        background: transparent;
        border: none;
        padding: 0 0 0 15px;
      }
    }
    &__items {
      margin-top: 40px;
    }
    &__item {
      &-title {
        i {
          width: 20px;
        }
        span {
          font-size: 15px;
        }
      }
      &-text {
        a {
          font-size: 15px;
        }
      }
    }
  }
  .breadcrumbs {
    padding: 15px 0 0 0;
    &__item {
      font-size: 14px;
    }
  }
}


@media screen and (max-width: 575px) {
  .menu {
    &__items {
      display: block;
    }
    &__item {
      margin-bottom: 0;
      &:not(:last-child) {
        margin: 0 0 20px 0;
      }
      &-text {
        a {
          display: block;
        }
        a:not(:first-child) {
          margin: 8px 0 0 0;
        }
      }
    }
    &__socials {
      margin-top: 40px;
    }
  }
  .display2 {
    font-size: 23px;
    padding-right: 55px;
    &:after {
      top: 15px;
      width: 40px;
    }
  }
}