.product-main {
  background: #EDF3FF;
  &__inner {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;
    z-index: 2;
    margin-top: 10px;
  }
  &__left {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 825px;
  }
  &__right {
    width: 740px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFF;
    border-radius: 8px 8px 8px 0px;
    padding: 50px;
  }
  &__title {
    display: inline-block;
    font-size: 52px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    padding-right: 135px;
    &:after {
      content: '';
      position: absolute;
      top: 31px;
      right: 0;
      width: 119px;
      height: 1px;
      background: #22A3DA;
    }
  }
  &__wrap {
    background: #FFF;
    box-shadow: 10px -10px 0px 0px #DEE8FD;
    border-radius: 8px;
    padding: 40px;
    margin-top: 30px;
  }
  &__list {
    li {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-bottom: 1px dashed #D7D7D7;
      }
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
    li span:nth-child(1) {
      font-size: 16px;
      line-height: 120%;
      color: #676767;
      background: #ffffff;
      padding: 0 0 3px;
      position: relative;
      z-index: 1;
    }
    li span:nth-child(2) {
      flex-shrink: 0;
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
      color: #363636;
      background: #ffffff;
      padding: 0 0 3px;
      position: relative;
      z-index: 1;
      margin-left: 15px;
    }
  }
  &__img {
    width: 500px;
    margin: 0 auto;
    img {
      width: 100%;
      margin-left: 30px;
    }
  }
}
.product-info {
  background-image: url("../img/product-info__bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 300px 0 85px;
  position: relative;
  margin-top: -230px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(32, 59, 99, 0.75);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__tabs {
    &-head {
      display: flex;
      align-items: center;
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 7px;
      }
      &::-webkit-scrollbar-track {
        background: #D1DEF2;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #53698A;;
        border-radius: 3px;
      }
    }
    &-body {
      background: #FFF;
      padding: 50px 50px 100px 50px;
    }
  }
  &__tab {
    flex-shrink: 0;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: transparent;
    border: 1px solid #22A3DA;
    border-radius: 5px;
    font-size: 18px;
    line-height: 120%;
    color: #ffffff;
    padding: 5px 35px;
    cursor: pointer;
    transition: all 0.3s;
    position: relative;
    margin-bottom: 40px;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: -40px;
      transform: translate(-50%, 0);
      width: 1px;
      height: 40px;
      background: #22A3DA;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      background: #22A3DA;
      &:after {
        opacity: 1;
        pointer-events: all;
      }
    }
    &:not(:first-child) {
      margin-left: 20px;
    }
  }
  .tab--active {
    background: #22A3DA;
    &:after {
      opacity: 1;
      pointer-events: all;
    }
  }
}
.product-desc {
  &__text {
    p {
      font-size: 15px;
      line-height: 150%;
      color: #272B2D;
    }
    p:not(:first-child) {
      margin-top: 20px;
    }
  }
  &__list {
    margin-top: 20px;
    li {
      font-size: 15px;
      line-height: 150%;
      color: #272B2D;
      position: relative;
      padding-left: 20px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 10px;
        height: 10px;
        background: #22A3DA;
        border-radius: 50%;
      }
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
}
.product-characteristic {
  &__list {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 100px / 2));
    gap: 45px 100px;
    li {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-bottom: 1px dashed #D7D7D7;
      }
    }
    li span:nth-child(1) {
      font-size: 16px;
      line-height: 120%;
      color: #676767;
      background: #ffffff;
      padding: 0 0 3px;
      position: relative;
      z-index: 1;
    }
    li span:nth-child(2) {
      flex-shrink: 0;
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
      color: #363636;
      background: #ffffff;
      padding: 0 0 3px;
      position: relative;
      z-index: 1;
      margin-left: 15px;
    }
  }
}
.product-model {
  &__group {
    &:not(:first-child) {
      margin-top: 45px;
    }
  }
  &__title {
    font-size: 18px;
    line-height: 120%;
    color: #363636;
  }
  &__table {
    margin-top: 10px;
    overflow-x: auto;
    padding-bottom: 10px;
    &::-webkit-scrollbar {
      height: 7px;
    }
    &::-webkit-scrollbar-track {
      background: #D1DEF2;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #53698A;;
      border-radius: 3px;
    }
  }
  table {
    width: 1440px;
    border-collapse: collapse;
    thead {
      tr {
        background: #828282;
        th {
          font-size: 17px;
          line-height: 120%;
          color: #ffffff;
          border: 1px solid #E7E7E7;
          padding: 10px 20px;
        }
      }
    }
    tbody {
      tr {
        td {
          font-size: 15px;
          line-height: 120%;
          color: #4A4A4A;
          text-align: center;
          border: 1px solid #E7E7E7;
          padding: 15px 20px;
        }
      }
    }
  }
  .table-items {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .table-item {
    position: relative;
    &:hover {
      .table-item__hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &:not(:first-child) {
      margin-left: 10px;
    }
    &__show {
      width: 29px;
      img {
        width: 100%;
      }
    }
    &__hidden {
      position: absolute;
      top: -100%;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100px;
      background: #ffffff;
      border: 1px solid #E7E7E7;
      padding: 5px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -30px;
        transform: translate(-50%, 0);
        border: 20px solid transparent;
        border-top: 20px solid #ffffff;
        pointer-events: none;
      }
    }
    &__text {
      font-size: 14px;
      line-height: 120%;
      color: #363636;
      text-align: center;
      position: relative;
      z-index: 1;
    }
  }
}
.product-documents {
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 35px;
    li {
      position: relative;
      padding-left: 55px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 100%;
        height: 2px;
        background: #22A3DA;
      }
      a {
        display: inline-block;
        font-size: 18px;
        line-height: 140%;
        color: #363636;
        position: relative;
        z-index: 2;
        background: #ffffff;
        padding: 0 15px;
      }
      .product-documents__flex {
        background-image: url("../img/download-icon.svg");
        background-repeat: no-repeat;
        background-position: top 50% left 15px;
        background-size: 25px;
        padding-left: 50px;
      }
    }
  }
}
.equipment {
  background: #EDF3FF;
  padding: 75px 0 110px;
  &__title {
    color: #363636;
  }
  &__slider {
    position: relative;
    margin-top: 40px;
  }
  &__swiper-wrapper {
    align-items: stretch;
  }
  &-card {
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__img {
      min-height: 320px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF;
      border-radius: 8px;
      padding: 20px;
      img {
        max-width: 100%;
      }
    }
    &__desc {
      margin-top: 20px;
    }
    &__title {
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      color: #363636;
    }
    &__list {
      margin-top: 20px;
      li {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 1px;
          border-bottom: 1px dashed #D7D7D7;
        }
        &:not(:first-child) {
          margin-top: 10px;
        }
        span:nth-child(1) {
          font-size: 16px;
          line-height: 120%;
          color: #676767;
          background: #EDF3FF;
          padding: 0 0 3px;
          position: relative;
          z-index: 1;
        }
        span:nth-child(2) {
          flex-shrink: 0;
          font-size: 18px;
          line-height: 120%;
          font-weight: 500;
          color: #363636;
          background: #EDF3FF;
          padding: 0 0 3px;
          position: relative;
          z-index: 1;
          margin-left: 10px;
        }
      }
    }
    &__btn {
      width: 100%;
      height: 45px;
      border: 1px solid #22A3DA;
      border-radius: 5px;
      margin-top: 20px;
      span {
        font-size: 18px;
        line-height: 120%;
        color: #363636;
        transition: all 0.3s;
      }
      &:hover {
        background: #22A3DA;
        span {
          color: #ffffff;
        }
      }
    }
  }
  .swiper-pagination {
    bottom: -45px !important;
  }
}


@media screen and (max-width: 1500px) {
  .product-main {
    &__left {
      width: 700px;
    }
    &__right {
      width: 600px;
    }
    &__img {
      width: 370px;
    }
  }
  .product-info {
    &__tabs {
      &-body {
        padding: 50px 30px 70px 30px;
      }
    }
  }
}

@media screen and (max-width: 1199px) {
  .product-main {
    &__left {
      width: 560px;
    }
    &__right {
      width: 550px;
      padding: 70px 20px;
    }
    &__title {
      font-size: 40px;
      &:after {
        top: 22px;
      }
    }
    &__img {
      width: 340px;
      margin: 0 0 0 auto;
      img {
        margin-left: 0;
      }
    }
  }
  .product-info {
    &__tabs {
      &-body {
        padding: 50px 25px;
      }
    }
  }
  .product-characteristic {
    &__list {
      grid-template-columns: repeat(2,calc(50% - 40px / 2));
      gap: 40px;
    }
  }
}

@media screen and (max-width: 991px) {
  .product-main {
    &__inner {
      margin-top: 25px;
    }
    &__left {
      width: 480px;
    }
    &__right {
      width: 330px;
      padding: 100px 15px;
    }
    &__wrap {
      padding: 30px;
    }
    &__list {
      li span:nth-child(1) {
        font-size: 14px;
      }
      li span:nth-child(2) {
        font-size: 16px;
      }
    }
    &__img {
      width: 200px;
    }
  }
  .product-info {
    padding-top: 250px;
    margin-top: -200px;
    &__tab {
      padding: 5px 22px;
      margin-bottom: 25px;
      &:after {
        bottom: -25px;
        height: 25px;
      }
    }
  }
  .product-characteristic {
    &__list {
      grid-template-columns: 100%;
      gap: 15px;
      li span:nth-child(1) {
        font-size: 14px;
      }
      li span:nth-child(2) {
        font-size: 16px;
      }
    }
  }
  .product-documents {
    &__list {
      gap: 20px;
      li {
        font-size: 15px;
        &:after {
          height: 1px;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .product-main {
    &__inner {
      flex-direction: column;
      margin-top: 15px;
    }
    &__left {
      position: static;
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
      padding: 30px 15px;
    }
    &__title {
      font-size: 30px;
      &:after {
        top: 17px;
      }
    }
    &__wrap {
      margin-top: 0;
    }
    &__img {
      width: 300px;
      margin: 0 auto;
    }
  }
  .product-info {
    padding-top: 560px;
    margin-top: -520px;
    &__tabs {
      &-body {
        padding: 30px 15px;
      }
    }
  }
}

@media screen and (max-width: 575px) {
  .product-main {
    &__title {
      font-size: 22px;
      padding-right: 50px;
      &:after {
        top: 13px;
        width: 45px;
      }
    }
    &__wrap {
      padding: 30px 15px;
    }
    &__img {
      width: 250px;
    }
  }
  .product-info {
    &__tab {
      padding: 5px 15px;
    }
  }
  .product-documents {
    &__list {
      li {
        padding-left: 30px;
        a {
          padding: 0 10px;
        }
        .product-documents__flex {
          background-position: top 50% left 10px;
          padding-left: 45px;
        }
      }
    }
  }
}